@font-face {
  font-family: 'Futura';
  font-weight: 300;
  src: local('futura'), url('./futura\ light\ bt.ttf');
}

@font-face {
  font-family: 'Futura';
  font-weight: 400;
  src: url('./Futura\ Book\ font.ttf');
}

@font-face {
  font-family: 'Futura';
  font-weight: 500;
  src: url('./futura\ medium\ bt.ttf');
}

@font-face {
  font-family: 'Futura';
  font-weight: 600;
  src: url('./Futura\ Heavy\ font.ttf');
}

@font-face {
  font-family: 'Futura';
  font-weight: bold;
  src: url('./futur.ttf') format('truetype');
  src: url('Futura-Bold.woff2') format('woff2'),
    url('Futura-Bold.woff') format('woff');
  font-weight: bold;
}

body {
  font-family: 'Futura', sans-serif;
}
